.App {
  text-align: center;
  background-color: #001544;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

button{
  width: 150px;
  height: 48px;
  border-radius: 125px;
  border: none;
  font-size: 17px;
  color: white;
  cursor: pointer;
}

button:active {
  box-shadow: -2px -2px 8px #c0c0c0 inset;
}

button:first-of-type{
  background: linear-gradient(#e86363, #c02323);
}

button:last-of-type{
  background: linear-gradient(#3B9AE1, #5800FF);
}

p{
  font-size: 40px;
  color: white;
  width: 120px;
}